.containerSearch {
  display: flex;
  flex-direction: row;
  justify-content:center;
  align-items: center;
  flex-wrap: nowrap;
}

.formSearch{
   display: flex;
   flex-direction: row;
   justify-content: space-between;
}

.inputSearch {
  /* width: 22em; */
  padding-left: 3.125em;
  padding-top: 0.625em;
  padding-bottom: 0.625em;
  font-size: 1.125em;
  border-radius: 20px;
  font-family: "Raleway", sans-serif;
  border: 0;
  outline: none;
  margin-right: 1%;

  background-image: url(data:image/png;base64,);
  background-size: 1.875em 1.875em;
  background-repeat: no-repeat;
  background-position-x: 0.625em;
  background-position-y: center;
}

.buttonSearch {
  
  border: 0;
  border: 2px solid white;
  border-radius: 15px;
  padding: 0.5em;
  
  background-color: #619ac8;
  color: white;
  font-size: 1.25em;
  font-family: "Raleway", sans-serif;
  margin-right: 1.25em;
  text-align: center;
  
}

.buttonSearch:hover {
  background-color: rgb(221, 86, 86);
  cursor: pointer;
}



.container {
  width: 100%;
  background-color: rgb(19, 42, 71);
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, 25rem );
  justify-items:center;
  justify-content: space-evenly;
  align-items: stretch;
  align-content: stretch;
  grid-column-gap: 2%;
  column-gap: 2%;
  
}
.nameApp {
  display: flex;
  align-items: center;
}

.logo {
  height: 4.375em;
  width: 5.625em;
}

.Title {
  width: -webkit-max-content;
  width: max-content;
  color: tomato;
  font-weight: bold;
  font-size: 2.1875em;
  font-family: "Raleway", sans-serif;
  margin-left: 0.9375em;
  text-shadow: 6px 3px 3px rgba(243,253,255,0.50);
}

