  .loader {
    position: fixed;
    top: calc(50% - 2.5em);
    left: calc(50% - 2.5em);
    background-color: transparent;

    border: 0.625em solid #b8b3b3;
    border-top: 0.625em solid rgb(19, 137, 167);
    border-radius: 50%;
    width: 6.25em;
    height: 6.25em;
    animation: spin 1s linear infinite;
  }
  
  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  
